import { Popup } from 'vant'

export default {
  components: {
    [Popup.name]: Popup
  },
  data() {
    return {
      // sign up
      name: '',
      account: '',
      password: '',
      surePassword: '',

      isExitAccount: false,
      isSurePasswordVerify: false,

      isShowSettingQuestion: false,
      passwordQuestion: '',
      passwordAnswer: ''
    }
  },
  computed: {
    isSign() {
      return !(this.name && this.account && this.password && this.surePassword)
    },
    isPasUnanimous() {
      return this.isSurePasswordVerify && this.password !== this.surePassword
    }
  },
  methods: {
    onRegisterInfo() {
      if (!this.isExitAccount && !this.isPasUnanimous) {
        this.$dialog
          .confirm({
            title: '提示',
            message: '还未设置安全问题，是否前往设置？',
            cancelButtonText: '否,去登录',
            confirmButtonText: '是，去设置'
          })
          .then(() => {
            this.isShowSettingQuestion = true
          })
          .catch(() => {
            this.reSaveUserInfo()
          })
      }
    },
    onSurePassword() {
      this.isSurePasswordVerify = true
    },
    reFindIsExitAccount() {
      this.$callApi({
        url: 'api/mobile/user/isExistAccount',
        method: 'post',
        data: {
          account: this.account
        }
      }).then(data => {
        this.isExitAccount = data.isExistAccount
      })
    },
    reSaveUserInfo() {
      return this.$callApi({
        url: 'api/mobile/user/save',
        method: 'post',
        data: {
          name: this.name,
          account: this.account,
          password: this.password,
          pwdQuestion: this.passwordQuestion,
          pwdQuestionAnswer: this.passwordAnswer
        }
      }).then(data => {
        // console.log(this.$color.success)

        this.$notify({
          message: '注册成功',
          duration: 1000,
          background: '#67c23a'
        })
        this.onPush('login')
      })
    },
    onPush(routerName) {
      this.$router.push({ name: routerName })
    }
  }
}
